import React from 'react';
import PropTypes from 'prop-types';

// Props do botao utilizado para mudar a cor do botão e do texto
function Button({ color, text, onClick }) {
  //função de criação de evento clique
  // (e) significa um evento objeto e exibe informações sobre o botão
  //const onClick = (e) => {
  //  console.log(e);
  //};

  return (
    <button
      onClick={onClick}
      style={{ backgroundColor: color }}
      className="btn"
    >
      {text}
    </button>
  );
}

// Definição da cor padrão
Button.defaultProps = {
  color: 'blue',
};

// Utilizando proptypes e definindo as propiedades como string
Button.propTypes = {
  text: PropTypes.string,
  color: PropTypes.string,
  onClick: PropTypes.func,
};

export default Button;
